<script>
    import {onMount} from 'svelte';
    import L from 'leaflet';

    const MY_ACCESS_TOKEN = 'pk.eyJ1IjoiZGFwcGRldnYiLCJhIjoiY2s3ZWo0dWV4MDYzcjNncHRncnlvdHY2OSJ9.zLk9G0tHK9iU-onN_TjxFw';
    let map, policeCarIcon, cLngLat;

    onMount(mapInit)

    function mapInit() {

        map = L.map('map').setView([51.505, -0.09], 13);

        L.tileLayer('https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token={accessToken}', {
            attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery © <a href="https://www.mapbox.com/">Mapbox</a>',
            maxZoom: 18,
            id: 'mapbox/streets-v11',
            tileSize: 512,
            zoomOffset: -1,
            accessToken: MY_ACCESS_TOKEN
        }).addTo(map);
    
        // let marker = L.marker([51.5, -0.09]).addTo(map);

        // let circle = L.circle([51.508, -0.11], {
        //     color: 'black',
        //     fillColor: 'green',
        //     fillOpacity: 0.3,
        //     radius: 1000
        // }).addTo(map);

        // marker.bindPopup("<b>Hello world!</b><br>I am a popup.").openPopup();
        // circle.bindPopup("I am a circle.");

        // Get user location
        map.locate({watch: false, setView: true, enableHighAccuracy: true});
        map.on('locationfound', locationFound);
        
        function locationFound(e) {
            L.marker(e.latlng, {icon: placeholderIcon}).addTo(map).bindPopup("I'am here!");
        }

        // Open context menu
        map.on('contextmenu', cmenu);
    
        function cmenu(e) {
            
            let el = document.getElementById('context-menu');

            let menuDiv = L.divIcon({html: el, className: 'context-menu'});
            
            let menu = L.marker(e.latlng, {icon: menuDiv}).addTo(map);
            
            menu.once({mouseout: menuClose})

            // map.openPopup(el, e.latlng)

            // let popup = L.popup({maxHeight: 200, autoPan: false, keepInView: true, className: 'context-menu'})
            // .setLatLng(e.latlng)
            // .setContent('<p>Что здесь?</p><button class="context-menu-item" on:click={addPoliceCar}>Патрульная машина ГИБДД</button>')
            // .openOn(map)

            cLngLat = e.latlng;
        }

        function menuClose(e) {
            e.target.remove()
        }

        // function onMapClick(e) {
            
        //     let popup = L.popup()
        //     .setLatLng(e.latlng)
        //     .setContent("You clicked the map at " + e.latlng)
        //     .openOn(map);
            
        //     L.marker(e.latlng, {icon: policeCarIcon}).addTo(map).bindPopup("Police car!");
        // }

        // map.on('click', onMapClick);

        policeCarIcon = L.icon({
            iconUrl: './icons/police-car.png',

            iconSize:     [64, 64], // size of the icon
            iconAnchor:   [32, 32], // point of the icon which will correspond to marker's location
            popupAnchor:  [0, -32] // point from which the popup should open relative to the iconAnchor
        });

        function addPoliceCar() {
            L.marker(e.latlng, {icon: policeCarIcon}).addTo(map).bindPopup("Police car!");
        }

        let placeholderIcon = L.icon({
            iconUrl: './icons/placeholder.png',

            iconSize:     [64, 64], // size of the icon
            iconAnchor:   [32, 32], // point of the icon which will correspond to marker's location
            popupAnchor:  [0, -32] // point from which the popup should open relative to the iconAnchor
        });
    
    }

    function addPoliceCar() {
        L.marker(cLngLat, {icon: policeCarIcon}).addTo(map).bindPopup("Police car!");
    }

</script>

<div class="pt-16">
    <div id="map" style="height: 800px;" class="z-0 w-full">
    </div>
    <div id="context-menu" class="context-menu">
        <p>Что здесь?</p>
        <button class="context-menu-item" on:click={addPoliceCar}>Патрульная машина ГИБДД</button>
        <button>Патрульная машина ГИБДД</button>
        <button>Патрульная машина ГИБДД</button>
    </div>
</div>