<script>
    import {firebase, db, googleProvider} from './../firebase.js';
    import firebaseui from 'firebaseui';
    import {FirebaseApp, User, Doc, Collection} from 'sveltefire';
    import { fade, fly } from 'svelte/transition';

    let isOpen = false;
    let email = '';
    let password = '';
    let username = '';
    let promocode = '';

    let ui = new firebaseui.auth.AuthUI(firebase.auth());

    function authUI() {

        ui.start('#firebaseui-auth-container', {
            callbacks: {
                signInSuccessWithAuthResult: function(authResult, redirectUrl) {
                // User successfully signed in.
                // Return type determines whether we continue the redirect automatically
                // or whether we leave that to developer to handle.
                return true;
                },
                uiShown: function() {
                // The widget is rendered.
                // Hide the loader.
                document.getElementById('loader').style.display = 'none';
                }
            },
            // signInFlow: 'popup',
            // signInSuccessUrl: '<url-to-redirect-to-on-success>',
            signInOptions: [
                // List of OAuth providers supported.
                firebase.auth.EmailAuthProvider.PROVIDER_ID,
                firebase.auth.PhoneAuthProvider.PROVIDER_ID,
                firebase.auth.GoogleAuthProvider.PROVIDER_ID
            ],
            // Other config options...
        });
    }

    function activatePromocode(code, uid) {
        let promoRef = db.collection('admin').doc('promo');
        let opt = {
            source: 'server'
        };
        promoRef.get(opt).then(function(doc) {
            if(doc.exists) {
                let data = doc.data();
                if(code in data.active) {
                    let userRef = db.collection('users').doc(uid);
                    userRef.update({
                        balance: data.active[code]
                    })
                    console.log('promocode activated!')
                    console.log('new balance: ', data.active[code])
                } else {
                    console.log('promocode not found')
                }
                console.log('doc.data', data)
                console.log('doc.active', data.active)
            } else {
                console.log('some error')
            }
        }).catch(function(error) {
            console.log("Error getting document:", error);
        });
    }


</script>

<div class="header fixed z-10 h-16 top-0 w-full bg-gray-900">
    <div class="header-menu absolute top-0 right-0 p-2 text-center">
        {#if !isOpen}
        <svg on:click={ () => isOpen = true }
            class="header-menu-icon"
            xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
            <path d="M0 0h24v24H0z" fill="none"/>
            <path d="M3 18h18v-2H3v2zm0-5h18v-2H3v2zm0-7v2h18V6H3z"/>
        </svg>
        {:else}
        <svg on:click={ () => isOpen = false }
            class="header-menu-icon"
            xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
            <path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"/>
            <path d="M0 0h24v24H0z" fill="none"/>
        </svg>
        {/if}
    </div>
    {#if isOpen}
    <div transition:fade="{{ duration: 200 }}" class="menu-container">
        <div class="user-auth">
            <FirebaseApp {firebase}>
                <User let:user let:auth>
                    <!-- User info -->
                    <div class="user-info">
                        <p> {user.email} </p>
                        <p> {user.displayName} </p>
                        <p> {user.uid} </p>
                    </div>
                    <!-- Account info -->
                    <div class="account-info">
                        <Doc path={`users/${user.uid}`} let:data>

                            <div slot="loading">
                                <p>loading...</p>
                            </div>

                            <div>
                                <p>Username: {data.name}</p>
                                <p>Balance: {data.balance}</p>
                                <p>Promocode: {data.promocode}</p>
                                <button class="menu-btn" on:click={()=>activatePromocode(data.promocode, user.uid)}>
                                activate
                                </button>
                            </div>
                            
                            <div slot="fallback">
                                <p> Complete registration: </p>
                                <input class="menu-input" type="text" placeholder="username" bind:value={username}>
                                <input class="menu-input" type="text" placeholder="promocode" bind:value={promocode}>
                                {#if username != null && ' '}
                                    <Collection path={`users`} let:ref={uRef} query={(uRef)=> uRef.where('name','==',username)} let:first>
                                        {#if first === username}
                                        <p>Username already exist</p>
                                        {:else}
                                        <p>Username avialable</p>
                                        {/if}
                                        <div slot="loading">
                                            <p>username check...</p>
                                        </div>
                                        <div slot="fallback">
                                            <p>username is free</p>
                                        </div>
                                    </Collection>
                                {/if}
                                <button class="menu-btn" on:click|once={()=> db.collection('users').doc(user.uid).set({name: username, uid: user.uid, balance: 0, promocode})}>Complete</button>
                            </div>
                            
                        </Doc>
                    </div>
                    <div>
                        <button class="menu-btn mt-4" on:click={()=> auth.signOut()}>LogOut</button>
                    </div>
                    <!-- Signed out -->
                    <div slot="signed-out">
                        <div id="loader">
                            <button on:click={authUI}>Login</button>
                        </div>
                            <div id="firebaseui-auth-container"></div>
                    </div>
                </User>
            </FirebaseApp>
        </div>
        <br>
        <hr>

        <!-- MENU LIST -->
        <ul class="menu-list mt-20" on:click={ () => isOpen = false }>
            <li><a href="#/">Home</a></li>
            <li><a href="#/about">About</a></li>
            <li><a href="#/">Page</a></li>
            <li><a href="#/">Login</a></li>
        </ul>
    </div>
    {/if}
</div>